const weather = {}
// textArr
for (let i = 0; i < textArr.length; i++) {
  const res = makePy(textArr[i])[0].toLowerCase();
  weather[textArr[i]] = {
    name: textArr[i],
    type: res,
    image: res
  }
}

const weather2 = {}
for (let key in textObj) {
	let t = textObj[key].split('_');
	const res = makePy(t[0])[0].toLowerCase();
	weather2[key] = {
		key: key,
		name: t[0],
		type: res,
		image: t[1] ? t[1] + res : res
	}
}

// 
const hasDN = ['', '', '', '', '' ,''];

function queryWeather(str, state = '') {
  const _obj = weather[str];
  const _has = hasDN.includes(str);
  return {
    ..._obj,
    image: _has ? state + "-" + _obj.image : _obj.image
  }
}

function queryWeather2(str, state = '') {
  const _obj = weather2[str];
  const _has = hasDN.includes(textObj[str]);
  return {
    ..._obj,
    image: _has ? state + "-" + _obj.image : _obj.image
  }
}

// 
function __getWinDByUV(u, v) {
	let fx = 999.9;
	if (u > 0 & v > 0) {
	fx = 270 - Math.atan(v / u) * 180 / Math.PI;
	} else if (u < 0 & v > 0) {
	fx = 90 - Math.atan(v / u) * 180 / Math.PI;
	} else if (u < 0 & v < 0) {
	fx = 90 - Math.atan(v / u) * 180 / Math.PI;
	} else if (u > 0 & v < 0) {
	fx = 270 - Math.atan(v / u) * 180 / Math.PI;
	} else if (u == 0 & v > 0) {
	fx = 180;
	} else if (u == 0 & v < 0) {
	fx = 0;
	} else if (u > 0 & v == 0) {
	fx = 270;
	} else if (u < 0 & v == 0) {
	fx = 90;
	} else if (u == 0 & v == 0) {
	fx = 999.9;
	}
}
function queryWinD(u, v) {
	const windDir = __getWinDByUV(u, v);
	return queryWinDir(windDir);
}
function queryWinDir(windDir) {
	if(windDir == 999999) return '-';
	else if (windDir >= 22.5 && windDir < 67.5) return "";
	else if (windDir >= 67.5 && windDir < 112.5) return "";
	else if (windDir >= 112.5 && windDir < 157.5) return "";
	else if (windDir >= 157.5 && windDir < 202.5) return "";
	else if (windDir >= 202.5 && windDir < 247.5) return "";
	else if (windDir >= 247.5 && windDir < 292.5) return "";
	else if (windDir >= 292.5 && windDir < 337.5) return "";
	else return "";
}
// 
function __getWinSByUV(u, v) {
	return Math.sqrt(Math.pow(u, 2) + Math.pow(v, 2));
}
function queryWinS(u, v) {
	const speed = __getWinSByUV(u, v);
	return queryWinSpeed(speed);
}

function queryWinSpeed(speed) {
	if(speed <= 0 || speed == 999999) return '-';
	else if(speed > 0 && speed <= 0.2) return '';
	else if(speed > 0.2 && speed <= 1.5) return '1';
	else if(speed > 1.5 && speed <= 3.3) return '2';
	else if(speed > 3.3 && speed <= 5.4) return '3';
	else if(speed > 5.4 && speed <= 7.9) return '4';
	else if(speed > 7.9 && speed <= 10.7) return '5';
	else if(speed > 10.7 && speed <= 13.8) return '6';
	else if(speed > 13.8 && speed <= 17.1) return '7';
	else if(speed > 17.1 && speed <= 20.7) return '8';
	else if(speed > 20.7 && speed <= 24.4) return '9';
	else if(speed > 24.4 && speed <= 28.4) return '10';
	else if(speed > 28.4 && speed <= 32.6) return '11';
	else if(speed > 32.6 && speed <= 36.9) return '12';
	else if(speed > 36.9 && speed <= 41.4) return '13';
	else if(speed > 41.4 && speed <= 46.1) return '14';
	else if(speed > 46.1 && speed <= 50.9) return '15';
	else if(speed > 50.9 && speed <= 56.0) return '16';
	else if(speed > 56.0 && speed <= 61.2) return '17';
	else if(speed > 61.2) return '17';
}
function dataCheck(data){
	if(data=='999999'){
		return '';
	}else{
		return data;
	}
};
function getEleName(data){
	if(data=='V_QUAL'){
		return '';
	}else if(data=='V_PPOLLU'){
		return '';
	}else if(data=='V15823'){
		return 'CO';
	}else if(data=='V15810'){
		return 'SO2';
	}else if(data=='V15826_01'){
		return 'O3 1';
	}else if(data=='V15826_08'){
		return 'O3 8';
	}else if(data=='PM2p5_Densty'){
		return 'PM2.5';
	}else if(data=='PM10_Densty_Fore'){
		return 'PM10';
	}else{
		return data;
	}
};
	
export {
	queryWeather,
	queryWeather2,
	queryWinD,
	queryWinS,
	queryWinSpeed,
	queryWinDir,
	dataCheck,
	getEleName
}