// 
const whiteList = [
	'/',
	'/pages/me/index',
	'/pages/index/index',
	//'/pages/product/index'
]
console.log("empty")
function hasPermission (url) {
	console.log("pass")
	let access_token = uni.getStorageSync('access_token');
	// token
	if(whiteList.indexOf(url) !== -1 || access_token) {
		return true;
	}
	return false;
}

uni.addInterceptor('navigateTo', {
	// , invoke
	invoke (e) {
		console.log("navigateTo:",e.url)
		if(!hasPermission(e.url)) {
			uni.showModal({
				title: '',
				content: '',
				showCancel: true,
				success: function (resbtn) {
					if (resbtn.confirm) {
						uni.reLaunch({
							url: '/pages/me/index'
						})
					}
				}
			});
			return false
		}
		return true
	},
	success (e) {
		// console.log(e)
	}
})

uni.addInterceptor('switchTab', {
	// tabbarH5
	invoke (e) {
		console.log("switchTab",e.url)
		if(!hasPermission(e.url)) {
			uni.showModal({
				title: '',
				content: '',
				showCancel: true,
				success: function (resbtn) {
					if (resbtn.confirm) {
						uni.reLaunch({
							url: '/pages/me/index'
						})
					}
				}
			});
			return false
		}
		return true
	},
	success (e) {
		 console.log(e,"switchTab")
	}
})